    padding-top: 5%;
    grid-template-rows: auto auto;
    grid-template-columns: 2fr 1fr; }
    body .container .item {
      text-align: center;
      margin: 1rem; }
      body .container .item:nth-child(1) {
        grid-column-start: 1;
        grid-column-end: -1; }
    body .container .search__container .search__form {
      -webkit-align-items: center;
              align-items: center;
      margin: auto 0;
      height: 100%;
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 2fr 1fr; }
      body .container .search__container .search__form .search__field .search__input {
