    body .container .queries__container {
      background: rgba(255, 255, 255, 0.5);
      border-radius: 1.5rem; }
      body .container .queries__container .query__container {
        background: rgba(255, 255, 255, 0.5);
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding: 1rem; }
        body .container .queries__container .query__container .query__button {
          padding: 0.5rem;
          background-color: #ff4b1f;
          color: #fff;
          text-transform: uppercase;
          font-weight: 600;
          border-radius: 0.5rem;
